<template lang="html">
  <!-- 跳转中转站 用于解决vue Router 组件复用问题 -->
</template>

<script>
export default {
  data(){
    return {

    }
  },
  created(){
    let query = this.$route.query;
    this.$router.replace({
      path:"/details",
      query
    })
  }
}
</script>

<style lang="css" scoped>

</style>



